.blacklist {
  background: linear-gradient(#1e1e2f, #1e1e24);
  width: calc(100% - 20rem);
  max-width: 100%;
  margin-top: -20px;
  min-height: calc(100vh - 90px);
  height: 100%;
  padding-top: 20px;
  margin-bottom: -30px;
  margin-left: 20rem;
  color: white;
  padding-left: 20px;
  padding-right: 20px;
}

.blacklist-container {
  padding-top: 10px;
}

.blacklist-grid {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr;
      grid-template-columns: 1fr;
  -ms-grid-rows: 1fr;
      grid-template-rows: 1fr;
  min-height: calc(100vh - 150px);
}

.blacklist-add-container {
  background-color: #27293d;
  border-radius: 10px;
}

.blacklist-add-container h3 {
  font-weight: 200;
  font-family: 'Poppins', sans-serif;
  margin-left: 20px;
  margin-right: 20px;
  padding-bottom: 5px;
  border-bottom: rgba(255, 255, 255, 0.1) 1px solid;
  color: #fff;
  font-size: 1.4375rem;
  padding-top: 10px;
}

.blacklist-button-row {
  display: flex;
  align-items: center;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 10px;
}

.blacklist-button-row input {
  width: 90%;
  max-width: 918px;
  background-color: transparent;
  outline: none;
  border: none;
  padding: 10px 18px;
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.75rem;
  border-radius: 0.4285rem;
  border: 1px solid #2b3553;
  margin-top: 8px;
  transition: border 0.2s;
}

.blacklist-button-row input:focus {
  border: 1px solid #1f8ef1;
  transition: border 0.2s;
}

.blacklist-button-row button {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 5px;
  height: 38px;
  padding-left: 10px;
  padding-right: 10px;
  outline: none;
  cursor: pointer;
  background-color: #1f8ef1;
  border: none;
  border-radius: 0.4285rem;
  color: #efefef;
  font-weight: 300;
  margin-top: 8px;
}

.blacklist-lister {
  display: flex;
  width: calc(100% - 40px);
  margin-left: 20px;
  margin-right: 20px;
  padding-bottom: 2px;
  padding-top: 2px;
  border-bottom: rgba(255, 255, 255, 0.1) 1px solid;
  color: #1f8ef1;
}

.license-list-db .group4 button {
  outline: none;
  border: none;
  padding-left: 10px;
  padding-right: 10px;
  background-color: #1f8ef1;
  border-radius: 10px;
  color: white;
  cursor: pointer;
}

.blacklist-error-group {
  padding-bottom: 10px;
}

.group1 p {
  word-wrap: break-word;
}

.blacklist-everysecond .blacklist-element:nth-of-type(odd) {
  background-color: #25273a;
}
