.dashboardarea {
  background: linear-gradient(#1e1e2f, #1e1e24);
  width: calc(100% - 20rem);
  max-width: 100%;
  margin-top: -20px;
  min-height: calc(100vh - 90px);
  height: 100%;
  padding-top: 20px;
  margin-bottom: -30px;
  margin-left: 20rem;
  color: white;
  padding-left: 20px;
  padding-right: 20px;
}

.dashboardarea-container {
  padding-top: 10px;
}

.dashboardarea-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 20px;
  gap: 20px;
  min-height: calc(100vh - 150px);
}

.latest-customers {
  background-color: #2a2e32;
  border-radius: 0.2857rem;
}

.latest-customers h4 {
  margin-left: 20px;
  margin-right: 20px;
  padding-left: 10px;
  padding-top: 20px;
  padding-bottom: 5px;
  color: #1f8ef1;
  text-transform: uppercase;
}

.latest-requests {
  background-color: #27293d;
  border-radius: 0.2857rem;
}

.latest-requests h5 {
  font-weight: 200;
  margin-left: 20px;
  padding-top: 10px;
  color: #9a9a9a;
}

.latest-requests h2 {
  font-weight: 200;
  font-family: "Poppins", sans-serif;
  margin-left: 20px;
  margin-right: 20px;
  padding-bottom: 5px;
  border-bottom: rgba(255, 255, 255, 0.1) 1px solid;
  color: #fff;
  font-size: 1.6875rem;
}

.latest-rejected h5 {
  font-weight: 200;
  margin-left: 20px;
  padding-top: 10px;
  color: #9a9a9a;
}

.latest-rejected h3 {
  font-weight: 200;
  font-family: "Poppins", sans-serif;
  margin-right: 20px;
  padding-bottom: 5px;
  color: #fff;
  font-size: 1.4375rem;
}

.latest-requests-group-container {
  margin-top: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 20px;
  margin-right: 20px;
}

.latest-request-group {
  margin-top: 10px;
  margin-bottom: 30px;
}

.latest-text-container p {
  padding-top: 15px;
  border-bottom: rgba(255, 255, 255, 0.1) 1px solid;
}

.latest-graph {
  background-color: #27293d;
  min-height: 20rem;
  border-radius: 0.2857rem;
  grid-row: 2;
  height: 100%;
}

.chart-container {
  margin-top: 10px;
  width: 100%;
  height: calc(100% - 90px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.ct-chart {
  height: 100%;
  width: 100%;
}

.latest-rejected {
  background-color: #27293d;
  border-radius: 0.2857rem;
  min-height: 20rem;
}

.latest-rejected h4 {
  margin-left: 20px;
  margin-right: 20px;
  padding-left: 10px;
  padding-top: 20px;
  padding-bottom: 5px;
  border-bottom: rgba(255, 255, 255, 0.1) 1px solid;
  color: #1f8ef1;
  text-transform: uppercase;
}

.chartist-tooltip {
  position: absolute;
  display: inline-block;
  opacity: 0;
  min-width: 5em;
  background: #fff;
  border-radius: 0.2857rem;
  color: #453d3f;
  font-weight: 700;
  text-align: center;
  pointer-events: none;
  z-index: 1;
  transition: opacity 0.2s linear;
}

.chartist-tooltip:before {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  margin-left: -15px;
  border: 15px solid transparent;
  border-top-color: #fff;
}

.chartist-tooltip.tooltip-show {
  opacity: 1;
}

.requests-container {
  display: flex;
  justify-content: space-between;
  margin-right: 20px;
  margin-left: 20px;
  padding-top: 10px;
  padding-bottom: 5px;
  border-bottom: rgba(255, 255, 255, 0.1) 1px solid;
  color: #1f8ef1;
  align-items: center;
}

.latestrequests-text h5 {
  font-weight: 200;
  color: #9a9a9a;
}

.latestrequests-text h3 {
  font-weight: 200;
  font-family: "Poppins", sans-serif;
  color: #fff;
  font-size: 1.4375rem;
}

.dot {
  height: 10px;
  width: 10px;
  border-radius: 50%;
}

.requests-container p {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
  color: gray;
  font-weight: 200;
  font-size: 0.83em;
}

.dot:first-child {
  background-color: #1f8ef1;
  font-size: 23px;
}

.requests-container span:nth-child(2) {
  background-color: #e14eca;
  font-size: 23px;
}

.under-text {
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.under-text h2 {
  font-size: 1.6875rem;
}

.under-text:nth-child(2) {
  font-size: 1.1875rem;
}

.under-text-rejected {
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.under-text-rejected h2 {
  font-size: 1.6875rem;
}

.under-text-rejected:nth-child(2) {
  margin-left: 20px;
  color: #2bffc6;
  font-size: 1.1875rem;
}

.ct-bar {
  stroke: #2bffc6 !important;
}

.yearly-texts {
  display: flex;
  align-items: center;
  grid-gap: 5px;
  gap: 5px;
}

.under-text-rejected {
  display: flex;
  justify-content: space-between;
  border-bottom: rgba(255, 255, 255, 0.1) 1px solid;
  margin-right: 20px;
}
